import Vue from 'vue'
import Router from 'vue-router'
import { getMdVue } from '../utils'
import ContentPage from '../views/ContentPage.vue'
import apiPages from './apiPages'
import docsPages from './docsPages'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: (_to, _from, savedPosition) => savedPosition || { x: 0, y: 0 },
  routes: [
    {
      path: '/',
      component: () => import(/* webpackChunkName: 'Home' */ '../views/Home.vue')
    },
    {
      path: '/docs/',
      component: () => import(/* webpackChunkName: 'Docs' */ '../views/Docs.vue'),
      children: Object.entries(docsPages).map(([path, component]) => {
        return { path, component }
      })
    },
    {
      path: '/docs/api/',
      component: () => import(/* webpackChunkName: 'DocsApi' */ '../views/DocsApi.vue'),
      children: Object.entries(apiPages).map(([path, component]) => {
        return { path, component }
      })
    },
    {
      path: '/privacy-policy',
      props: { boxClass: 'content' },
      component: ContentPage,
      children: [
        {
          path: '',
          component: () => getMdVue(import('../content/privacy-policy.md'))
        }
      ]
    },
    {
      path: '/newsletter/',
      component: ContentPage,
      children: [
        {
          path: 'confirm',
          props: queryProps,
          component: () => import(/* webpackChunkName: 'newsletter' */ '../views/newsletter/Confirm.vue')
        },
        {
          path: 'unsubscribe',
          props: queryProps,
          component: () => import(/* webpackChunkName: 'newsletter' */ '../views/newsletter/Unsubscribe.vue')
        },
        {
          path: 'compose',
          props: queryProps,
          component: () => import('../views/newsletter/Compose.vue')
        },
        {
          path: 'compose/:campaignId',
          props: queryProps,
          component: () => import('../views/newsletter/Compose.vue')
        }
      ]
    },
    {
      path: '/pro',
      component: () => import('../views/Pro/Index.vue'),
      children: [
        {
          path: '',
          component: () => import('../views/Pro/HeroBody.vue')
        },
        {
          path: 'checkout',
          component: process.env.NODE_ENV === 'production'
            ? { render: c => c('div') }
            : () => import('../views/Pro/Checkout.vue')
        }
      ]
    },
    {
      path: '/pro/docs',
      component: () => import('../views/ProDocs.vue'),
      children: Object.entries({
        '': () => getMdVue(import('../content/docs/index.md')),
        'sortable-swappable': () => getMdVue(import('../content/docs/sortable-swappable.md'))
      }).map(([path, component]) => ({ path, component }))
    },
    {
      path: '/pro/license',
      component: ContentPage,
      props: { boxClass: 'content' },
      children: [
        {
          path: '',
          component: () => getMdVue(import('../content/pro-license.md'))
        }
      ]
    }
  ]
})

export default router

function queryProps (route: { query: any, params: any }) {
  return { ...route.query, ...route.params }
}
